import { get, post } from '../../libs/request.js';
export default {
	data() {
		return {
			checked: [],
			tips:"",
			sn:""
		};
	},
	onLoad(e){
		this.sn = e.sn;
		this.getOrderTips(this.sn);
	},
	methods: {
		changed(e) {
			this.checked = e.detail.value;
		},
		submit(e) {
			let sn = this.sn;
			if(this.checked.length > 0){

			this.getWechart(sn);




			} else {
				uni.showToast({
					title:'请勾选协议'
				});
			}
		},
		getWechart(sn) {
			let _this = this;
			post('wxpay/payscore', {
				sn: sn
			}).then(res => {
				console.info('step1',res.data);
				if (res.code === 200) {
					wx.openBusinessView({
						businessType: 'wxpayScoreUse',
						extraData: {
							mch_id: res.data.mchId,
							package: res.data.packageInfo,
							timestamp: res.data.timestamp,
							nonce_str: res.data.nonceStr,
							sign_type: res.data.signType,
							sign: res.data.sign
						},
						success() {
							//dosomething
							console.info('step2')
							wx.navigateToMiniProgram({
								appId: 'wxd8f3793ea3b935b8',
								path: 'pages/use/use',
								extraData: {
									mch_id: res.data.mchId,
									package: res.data.packageInfo,
									timestamp: res.data.timestamp,
									nonce_str: res.data.nonceStr,
									sign_type: res.data.signType,
									sign: res.data.sign
								},
								success() {
									console.info('step3')
									
								},
								fail() {
									//dosomething
								},
								complete() {
									//dosomething
									console.info('complete')
									uni.navigateTo({
										url: `loaning?orderNum=${res.data.orderNum}`,
									});
								}
							});
						},
						fail() {
							//dosomething
							uni.showToast({
								title:'授权失败'
							})
							uni.navigateTo({
								url: "index"
							});
						},
						complete() {
							//dosomething
							uni.navigateTo({
								url: "index"
							});
						}
					});
				}
			});
		},
		getAliPay(sn) {
			post('/wxpay/freeze', {
				sn: sn
			}).then(res => {
				if(res.code === 200){




















				}
			});
		},
		getOrderTips(sn){
			post('/order/bill', {
				sn: sn
			}).then(res => {
				this.tips = res.data;
			})
			
		}
	}
};
